body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  padding: 15px;
}

#root {
  background-image: -webkit-linear-gradient(#00fdff45 0%, #afbb0047 100%);
  background-image: linear-gradient(#00fdff45 0%, #afbb0047 100%);
  height: 100%;
  min-height: 100vh;
}

.search-field {
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.26);
}

.main-weather {
  background-color: rgba(238, 238, 238, 0.37);
  font-size: 1.4em;
  border-radius: 12px;
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.26);
  margin-top: 10px;
  color: rgba(0,0,0,0.75);
  vertical-align: middle;
  height: 370px;
  width: 100%;
  padding: 15px;
  padding-top: 59px;
  font-size: 1.4em;
  margin-bottom: 10px;
}

.clear-cross {
  padding-top: 8px;
  display: inline-block;
  position: absolute;
  right: 2vw;
  width: 38px;
  height: 38px;
  z-index: 2;
  cursor: pointer;
}

.search-field {
  border-radius: 25px;
}

#search-field {
  border-radius: 25px;
  z-index: 1;
}

.bookmark-btn {
  color: #fff;
  background-color: rgba(0, 123, 255, 0.57);
  border-color: rgb(97, 178, 250);
}

.bookmark-btn:focus {
  box-shadow: none;
}

.bookmark-btn:hover {
  background-color: rgba(0, 123, 255, 0.57);
  border-color: rgba(0, 123, 255, 0.78);
}

.weather-img {
  width: 6em;
  height: 6em;
}

.main-weather-oneliner {
  margin-top: 98px;
  font-size: 1rem;
  color: rgba(0, 0, 0, 0.5);
}

.bookmarked-place {
  display: block;
  width: 100%;
  margin-bottom: 5px;
  box-shadow: 0px 1px 5px 0px rgba(0,0,0,0.26);
  background-color: rgb(97, 178, 250);
  border-color: rgb(97, 178, 250);
}

.empty-bm-notifier {
  color: rgba(0, 0, 0, 0.5);
  cursor: default;
  font-weight: 300;
}


@media only screen  and (min-width : 1224px) {
  #root {
    height: 100vh;
  }

  .main-weather {
    width: 60%;
    display: inline-block;
    margin-top: 0;
  }

  .bookmark-btn:hover {
    color: #fff;
    background-color: #0069d9;
    border-color: #0062cc;
  }

  .search-field {
    width: 38.5%;
    position: relative;
    left: 61%;
    height: 0;
  }

  .bookmarked-places {
    display: inline-block;
    width: 38%;
    margin-left: 11px;
    margin-top: 19px;
    height: 353px;
    vertical-align: top;
    padding-top: 27px;
    overflow-x: auto;
  }

  .clear-cross {
    font-size: 1.4em;
    right: 0.5vw;
    padding-top: 0;
    color: rgba(0, 0, 0, 0.61);
  }

  .empty-bm-notifier {
    position: relative;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    display: block;
  }
}
